// Define types for Contact
interface Contact {
  id: number;
  name: string;
  email: string;
  mobile: string;
}


interface HomeProps {
  contacts: Contact[]; // Expecting an array of contacts as a prop
}

export default async function Page() {
  // return <h1>Hello, Next.js!</h1>
  const res = await fetch("http://localhost:5001/api/contacts");
  const contacts = await res.json();
  
  return (
    <ul>
      {contacts.map((contact: any) => (
        <li key={contact.id}>
        <h2>{contact.name}</h2>
        <p>{contact.mobile}</p>
        <p>{contact.email}</p>
        {/* <button onClick={() => handleEditContact(contact)}>Edit</button>
        <button onClick={handleDelete}>Delete</button> */}
      </li>
      ))}
    </ul>
  )
}
